<h2 id="zi-ding-yi-dan-chu-kuang-xian-shi-fang-shi"><a class="header-anchor" href="#zi-ding-yi-dan-chu-kuang-xian-shi-fang-shi" aria-hidden="true">¶</a> 自定义弹出框显示方式</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-dropdown trigger=\&quot;custom\&quot; v-model=\&quot;visiable\&quot;&gt;\n    &lt;xl-button&gt;下拉菜单&lt;/xl-button&gt;\n    &lt;xl-dropdown-menu slot=\&quot;dropdown\&quot;&gt;\n        &lt;xl-dropdown-item&gt;黄金糕&lt;/xl-dropdown-item&gt;\n        &lt;xl-dropdown-item&gt;狮子头&lt;/xl-dropdown-item&gt;\n        &lt;xl-dropdown-item disabled&gt;螺蛳粉&lt;/xl-dropdown-item&gt;\n        &lt;xl-dropdown-item&gt;双皮奶&lt;/xl-dropdown-item&gt;\n        &lt;xl-dropdown-item&gt;蚵仔煎&lt;/xl-dropdown-item&gt;\n    &lt;/xl-dropdown-menu&gt;\n&lt;/xl-dropdown&gt;\n&lt;xl-button @on-click=\&quot;visiable=!visiable\&quot;&gt;{{visiable?'隐藏':'展开'}}&lt;/xl-button&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-dropdown trigger="custom" v-model="visiable">
    <xl-button>下拉菜单</xl-button>
    <xl-dropdown-menu slot="dropdown">
        <xl-dropdown-item>黄金糕</xl-dropdown-item>
        <xl-dropdown-item>狮子头</xl-dropdown-item>
        <xl-dropdown-item disabled>螺蛳粉</xl-dropdown-item>
        <xl-dropdown-item>双皮奶</xl-dropdown-item>
        <xl-dropdown-item>蚵仔煎</xl-dropdown-item>
    </xl-dropdown-menu>
</xl-dropdown>
<xl-button @on-click="visiable=!visiable">{{visiable?'隐藏':'展开'}}</xl-button>
</div>
                            <div slot="desc"><p>使用<code>trigger=&quot;custom&quot;</code>指定响应事件</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">'custom'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'visiable'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>下拉菜单<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-menu</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dropdown"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-item</span>&gt;</span>黄金糕<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-item</span>&gt;</span>狮子头<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-item</span> <span class="hljs-attr">disabled</span>&gt;</span>螺蛳粉<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-item</span>&gt;</span>双皮奶<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-dropdown-item</span>&gt;</span>蚵仔煎<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown-menu</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-dropdown</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">@on-click</span>=<span class="hljs-string">'visiable=!visiable'</span>&gt;</span>{{visiable?'隐藏':'展开'}}<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
</code></pre>
</div></demo-block>
