<h2 id="dropdown-shu-xing"><a class="header-anchor" href="#dropdown-shu-xing" aria-hidden="true">¶</a> Dropdown 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>设置是否显示或隐藏，支持 v-model</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>trigger</td>
<td>触发下拉框显示的方式</td>
<td>string</td>
<td>hover/click/custom</td>
<td>hover</td>
</tr>
<tr>
<td>popup-container</td>
<td>弹出框挂载的节点元素，默认 body 元素</td>
<td>function</td>
<td>-</td>
<td>()=&gt;document.body</td>
</tr>
<tr>
<td>hide-on-click</td>
<td>点击弹出框时是否关闭弹出框（trigger 不为 custom 且点击的条目不是 disabled 时有效）</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>space</td>
<td>弹出框距离被显示框的距离（为 number 类型时默认单位是 em）</td>
<td>number / boolean</td>
<td>-</td>
<td>0.5em</td>
</tr>
<tr>
<td>divide</td>
<td>是否显示分割线</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
</tbody>
</table>
<h2 id="dropdown-slots"><a class="header-anchor" href="#dropdown-slots" aria-hidden="true">¶</a> Dropdown slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>显示框插槽</td>
</tr>
<tr>
<td>dropdown</td>
<td>下拉框插槽</td>
</tr>
</tbody>
</table>
<h2 id="dropdownmenu-slots"><a class="header-anchor" href="#dropdownmenu-slots" aria-hidden="true">¶</a> DropdownMenu slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>菜单项插槽</td>
</tr>
</tbody>
</table>
<h2 id="dropdownitem-shu-xing"><a class="header-anchor" href="#dropdownitem-shu-xing" aria-hidden="true">¶</a> DropdownItem 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="dropdownitem-shi-jian"><a class="header-anchor" href="#dropdownitem-shi-jian" aria-hidden="true">¶</a> DropdownItem 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-click</td>
<td>点击事件</td>
<td>点击元素 e</td>
</tr>
</tbody>
</table>
<h2 id="dropdownitem-slots"><a class="header-anchor" href="#dropdownitem-slots" aria-hidden="true">¶</a> DropdownItem slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>菜单内容</td>
</tr>
</tbody>
</table>
</section></template>

